@charset "UTF-8";
@import url(https://fonts.googleapis.com/css?family=Overpass:regular,600,700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Ubuntu:700&display=swap);
@import url(https://fonts.googleapis.com/css?family=Poppins:regular&display=swap);
body {
    font-size: calc(13px + 3 * ((100vw - 320px) / 850));
    font-family: "Overpass";
    font-weight: normal;
    color: #6e7891;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: "Ubuntu";
    color: #1a0e4b;
}

h1 {
    font-size: calc(45px + 35 * ((100vw - 320px) / 850));
}

h2 {
    font-size: calc(23px + 22 * ((100vw - 320px) / 850));
}

h3 {
    font-size: 23px;
}
h3:hover {
    color: #ffffff;
}

h4 {
    font-size: 25px;
    font-weight: bold;
    color: #ffffff;
}

a {
    color: #1a0e4b;
}

p {
    font-size: calc(13px + 3 * ((100vw - 320px) / 850));
    color: #6e7891;
    line-height: 1.9;
    font-weight: normal;
}

html, body {
    height: 100%;
}

.wrapper {
    display: flex;
    flex-direction: column;
    overflow: hidden;
}

main {
    flex: 1 1 auto;
}

[class*=__container] {
    max-width: 1170px;
    margin: 0 auto;
    padding: 0 15px;
}

.header__container {
    padding: 20px 15px;
}

.logo:hover {
    opacity: 0.7;
}

.nav-link {
    color: #1a0e4b;
    font-weight: 600;
    line-height: 1.7;
    letter-spacing: 0.3px;
}
.nav-link:hover {
    color: #598117;
}

.navbar-expand-lg .navbar-nav {
    gap: 25px;
}

.welcome {
    height: 100hv;
    min-height: 1060px;
    padding-top: 190px;
}

.welcome-img {
    width: 100%;
}

.welcome-text {
    color: #373e39;
    max-width: 500px;
    margin: 30px 0;
}

.main-content {
    flex-direction: column;
}

.content-text {
    max-width: 520px;
    margin: 0 auto;
    margin-bottom: 75px;
}

.features {
    padding-top: 130px;
}

.feature-card {
    background-color: #f6f8f3;
    transition: color 0.5s ease 0s , background 0.5s ease 0s;
}
.feature-card:hover {
    background-color: #668b16;
}
.feature-card:hover .card-title {
    color: #fff;
}

.feature-cols {
    margin-left: -40px;
}

.feature-card {
    width: 570px;
    padding: 20px 25px;
    margin-left: 20px;
}

.card-body-features {
    padding-left: 35px;
}

.card-img {
    width: 100%;
    text-align: center;
}

.card-title {
    color: #1a0e4b;
}

.card-text {
    line-height: 1.7;
}

.members {
    padding-top: 230px;
}

.members-content {
    position: relative;
}

.members-text {
    max-width: 520px;
    margin: 0 auto;
}

.card-member {
    margin-top: 165px;
}

.members-img {
    border-radius: 50%;
    border: 5px dashed #fff;
    position: absolute;
    z-index: 99;
    top: 200px;
}

.members-card {
    width: 240px;
    height: 300px;
    border-radius: 50px;
    background-color: #668b16;
    display: flex;
    flex-direction: column;
    text-align: center;
    justify-content: end;
    margin-left: 10px;
    transition: background 0.5s ease 0s;
}
.members-card:hover {
    background-color: #29abe2;
}

.members-work {
    padding: 15px 0 45px 0;
    color: #ffffff;
}

.products {
    padding-top: 115px;
}

.products-item {
    padding: 30px 52px 30px 30px;
    margin-bottom: 15px;
    color: #1a0e4b;
    border-radius: 8px;
    box-shadow: 0 0 25px 0 rgba(34, 34, 34, 0.08);
    border: solid 1px #f4f1fd;
    transition: background 0.5s ease 0s;
}
.products-item:last-child {
    margin-bottom: 0;
}
.products-item::before {
    content: "";
    display: inline-block;
    height: 12px;
    width: 12px;
    border-radius: 50%;
    border: 3px solid #6e7891;
    margin-right: 20px;
}
.products-item:hover {
    background-color: #668b16;
}

.products-hodler {
    position: relative;
    min-height: 710px;
}

.products-tab {
    position: absolute;
    left: 0;
    right: 0;
    opacity: 0;
    visibility: hidden;
    pointer-events: none;
    transition: visibility 0.5s ease 0s , opacity 0.5s ease 0s;
}
.products-tab.active {
    opacity: 1;
    visibility: visible;
    pointer-events: all;
}

.product-card {
    position: relative;
    padding: 30px 30px 29px;
    border-radius: 4px;
    box-shadow: 0 0 25px 0 rgba(34, 34, 34, 0.08);
    background-color: #fff;
}

.product-img {
    padding-bottom: 60px;
    border-bottom: 3px solid #e5f0d2;
    margin-bottom: 35px;
}

.brand-product {
    position: absolute;
    top: 240px;
    left: 20%;
    background-color: #e5f0d2;
    padding: 5px 20px;
    border-radius: 12px;
}

.card-price {
    color: #ff3600;
}

.events {
    background-color: #668b16;
    margin-top: 120px;
}

.events__container {
    padding-top: 90px;
}

.events-card {
    width: 570px;
}

.card-img {
    width: 180px;
}

.data-text {
    font-size: 15px;
    color: #1a0e4b;
    font-weight: 700;
}

.speaker-name {
    margin-left: 25px;
}

.name {
    color: #1a0e4b;
    font-weight: 700;
}

.job {
    font-size: 13px;
}

.footer-list {
    margin-top: 65px;
}

.space-between {
    justify-content: space-between;
}

.footer-link,
.footer-text {
    font-size: 15px;
    font-family: "Poppins";
    line-height: 2.7;
    font-weight: normal;
    color: #ffffff;
}

.footer-link:hover {
    text-decoration: underline;
}

.footer-text {
    font-family: "Poppins";
    font-weight: 400;
}

.copyright {
    font-size: 14px;
    color: #ffffff;
    font-weight: 2.9;
    border-top: 1px solid #82a33b;
    margin: 80px 0 10px 0;
    padding-top: 20px;
}

