    background-color: #668b16;
    color: #ffffff;
    border-radius: 30px;
    padding: 15px 40px;
    transition: color 0.5s ease 0s , background 0.5s ease 0s;
}
.btn:hover {
    background-color: #29abe2;
    text-decoration: none;
    border: none;
